import React from 'react';
import './index.css';
import PropTypes from 'prop-types';

const CircularImage = ({ src }) => {
  return (
    <div className="circular-wrapper">
      <img src={src} />
    </div>
  )
};

CircularImage.propTypes = {
  src: PropTypes.string.isRequired
};

export default CircularImage;


