import React from 'react';
import './index.css';
import Text from '../../Components/Text';

const text = `
Full Stack Software Development Engineer with 1+ years of experience in building impactful software for the real-world. Passionate about tech and motivated by problem solving in the front-end space. Have a keen interest of JS, HTML, CSS and many web-related technologies and frameworks.
`
const Summary = () => {
  return (

    <div className="summary-wrapper">
      <Text text={text} />
    </div>

  )
};

export default Summary;


