import React from 'react';
import './index.css';
import PropTypes from 'prop-types';

function getClass(type) {
  switch (type) {
    case 'heading':
      return 'heading';
    default:
      return 'normal';
  }
}

const Text = ({ text, type }) => {
  return (
    <p className={getClass(type)}>{text}</p>
  )
};

Text.propTypes = {
  text: PropTypes.string.isRequired,
  type: PropTypes.string
};

Text.defaultProps = {
  type: 'normal'
};

export default Text;



