import React, { Component } from 'react';
import './App.css';

import CircularImage from './Components/CircularImage';
import Text from './Components/Text';
import Summary from './Containers/Summary';

class App extends Component {
  render() {
    return (
      <div className="App">
        <CircularImage src="http://placehold.it/200X200" />
        <Text text="Shobhit Chittora" type='heading' />
        <Text text="Full Stack Software Engineer | Front-end Dev | React | Redux | Angular | RxJS | Node" />
        <Summary />
      </div>
    );
  }
}

export default App;



